                  <span class="text-sm font-semibold text-gray-700"></span>
                </div>
              </div>
            </div>

            <!-- Logic Rules List -->
            <div class="divide-y divide-gray-100">
              <div
                v-for="(logicItem, index) in selectedItem.logics"
                :key="logicItem.uid"
                class="p-4 hover:bg-gray-50 transition-colors duration-200"
              >
                <div class="grid grid-cols-3 gap-4">
                  <!-- Source Selection -->
                  <div class="space-y-2">
                    <div class="flex items-center justify-between">
                      <span class="text-xs font-medium text-gray-500 uppercase tracking-wide"></span>
                      <button
                        @click="$emit('delete-logic-item', { index })"
                        class="w-6 h-6 bg-red-500 hover:bg-red-600 text-white rounded-md text-xs transition-colors duration-200 flex items-center justify-center"
                        title=""
                      >
                        <font-awesome-icon :icon="['fas', 'times']" class="h-3 w-3" />
                      </button>
                    </div>

                    <template v-if="!logicItem.default">
                      <select
                        :value="logicItem.default"
                        @change="$emit('update-logic-item-property', {index, property: 'default', value: $event.target.value})"
                        class="w-full px-3 py-2 text-sm border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                      >
                        <option value="" disabled></option>
                        <option
                          v-for="option in selectedItem.options"
                          :value="option.value"
                          :key="option.value"
                          :disabled="logicItem.default !== ''"
                        >
                          {{ option.label }}
                        </option>
                      </select>
                    </template>
                    <template v-else>
                      <div class="px-3 py-2 bg-gray-100 border border-gray-200 rounded-md">
                        <span class="text-sm text-gray-700 font-medium">{{ getSelectedOptionLabel(logicItem.default) }}</span>
                      </div>
                    </template>
                  </div>

                  <!-- Target Selection -->
                  <div class="space-y-2" v-if="logicItem.default">
                    <span class="text-xs font-medium text-gray-500 uppercase tracking-wide"></span>
                    <template v-if="!logicItem.destination">
                      <select
                        :value="logicItem.destination"
                        @change="$emit('update-logic-item-property', {index, property: 'destination', value: allBuilderOptions.find(opt => opt.name === $event.target.value) || $event.target.value })"
                        class="w-full px-3 py-2 text-sm border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                      >
                        <option value="" disabled></option>
                        <option
                          v-for="option in allBuilderOptions"
                          :value="option.name"
                          :key="option.name"
                        >
                          {{ option.name }}
                        </option>
                      </select>
                    </template>
                    <template v-else>
                      <div class="px-3 py-2 bg-gray-100 border border-gray-200 rounded-md">
                        <span class="text-sm text-gray-700 font-medium">{{ logicItem.destination.name }}</span>
                      </div>
                    </template>
                  </div>

                  <!-- Action Selection -->
                  <div class="space-y-2" v-if="logicItem.destination">
                    <span class="text-xs font-medium text-gray-500 uppercase tracking-wide"></span>
                    <template v-if="!logicItem.method">
                      <select
                        :value="logicItem.method"
                        @change="$emit('set-logic-item-method', {index, methodValue: $event.target.value, originalSelectedItem: selectedItem})"
                        class="w-full px-3 py-2 text-sm border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                      >
                        <option value="" disabled></option>
                        <option value="show"></option>
                        <option value="hide"></option>
                      </select>
                    </template>
                    <template v-else>
                      <div class="px-3 py-2 rounded-md flex items-center space-x-2"
                           :class="logicItem.method === 'show' ? 'bg-green-100 border border-green-200' : 'bg-red-100 border border-red-200'">
                        <font-awesome-icon 
                          :icon="['fas', logicItem.method === 'show' ? 'eye' : 'eye-slash']" 
                          :class="logicItem.method === 'show' ? 'text-green-600' : 'text-red-600'"
                          class="h-3 w-3" 
                        />
                        <span class="text-sm font-medium"
                              :class="logicItem.method === 'show' ? 'text-green-700' : 'text-red-700'">
                          {{ logicItem.method === 'show' ? '' : '' }}
                        </span>
                      </div>
                    </template>
                  </div>
                </div>
              </div>
            </div>

            <!-- Add Rule Button -->
            <div class="bg-gray-50 border-t border-gray-200 px-4 py-3 rounded-b-lg">
              <button
                class="inline-flex items-center px-4 py-2 bg-blue-600 hover:bg-blue-700 text-white text-sm font-medium rounded-md shadow-sm transition-colors duration-200 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500"
                @click="$emit('add-new-logic-rule', {selectedItemOriginal: selectedItem})"
              >
                <font-awesome-icon :icon="['fas', 'plus']" class="h-4 w-4 mr-2" />
                
              </button>
            </div>
          </div>
        </template>

        <!-- Dependent Logic Display Section -->
        <template v-else>
          <div class="bg-amber-50 border border-amber-200 rounded-lg shadow-sm">
            <!-- Info Header -->
            <div class="bg-amber-100 border-b border-amber-200 px-4 py-3 rounded-t-lg">
              <div class="flex items-center space-x-3">
                <div class="w-8 h-8 bg-amber-200 rounded-lg flex items-center justify-center">
                  <font-awesome-icon :icon="['fas', 'info-circle']" class="h-4 w-4 text-amber-700" />
                </div>
                <div>
                  <h4 class="text-sm font-semibold text-amber-800"></h4>
                  <p class="text-xs text-amber-700"></p>
                </div>
              </div>
            </div>

            <!-- Logic Display Header -->
            <div class="bg-gray-50 border-b border-gray-200 px-4 py-3">
              <div class="grid grid-cols-3 gap-4 text-center">
                <div class="flex items-center justify-center space-x-2">
                  <font-awesome-icon :icon="['fas', 'form']" class="h-4 w-4 text-gray-600" />
                  <span class="text-sm font-semibold text-gray-700"></span>
                </div>
                <div class="flex items-center justify-center space-x-2">
                  <font-awesome-icon :icon="['fas', 'check-circle']" class="h-4 w-4 text-gray-600" />
                  <span class="text-sm font-semibold text-gray-700"></span>
                </div>
                <div class="flex items-center justify-center space-x-2">
                  <font-awesome-icon :icon="['fas', 'magic']" class="h-4 w-4 text-gray-600" />
                  <span class="text-sm font-semibold text-gray-700"></span>
                </div>
              </div>
            </div>

            <!-- Logic Display Content -->
            <div class="divide-y divide-gray-100">
              <div
                v-for="logicItem in selectedItem.logics"
                :key="logicItem.uid"
                class="p-4"
              >
                <div class="grid grid-cols-3 gap-4 text-center">
                  <!-- Parent Form -->
                  <div class="flex items-center justify-center">
                    <div class="px-3 py-2 bg-blue-100 border border-blue-200 rounded-md">
                      <span class="text-sm font-medium text-blue-700">
                        {{ getParentLogicObjectName(logicItem) }}
                      </span>
                    </div>
                  </div>

                  <!-- Selected Value -->
                  <div class="flex items-center justify-center">
                    <div class="px-3 py-2 bg-gray-100 border border-gray-200 rounded-md">
                      <span class="text-sm font-medium text-gray-700">
                        {{ logicItem.default }}
                      </span>
                    </div>
                  </div>

                  <!-- Action Result -->
                  <div class="flex items-center justify-center">
                    <div class="px-3 py-2 rounded-md flex items-center space-x-2"
                         :class="logicItem.method === 'show' ? 'bg-green-100 border border-green-200' : 'bg-red-100 border border-red-200'">
                      <font-awesome-icon 
                        :icon="['fas', logicItem.method === 'show' ? 'eye' : 'eye-slash']" 
                        :class="logicItem.method === 'show' ? 'text-green-600' : 'text-red-600'"
                        class="h-3 w-3" 
                      />
                      <span class="text-sm font-medium"
                            :class="logicItem.method === 'show' ? 'text-green-700' : 'text-red-700'">
                        {{ logicItem.method === "show" ? "" : "" }}
                      </span>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </template>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'LogicTab',
  props: {
    selectedItem: {
      type: Object,
      required: true,
    },
    allBuilderOptions: {
      type: Array,
      default: () => [],
    },
    getParentLogicObjectNameFunction: {
      type: Function,
      required: true,
    }
  },
  emits: [
    'delete-logic-item',
    'update-logic-item-property',
    'set-logic-item-method',
    'add-new-logic-rule'
  ],
  methods: {
    getSelectedOptionLabel(value) {
      if (this.selectedItem && this.selectedItem.options) {
        const option = this.selectedItem.options.find(o => o.value === value);
        return option ? option.label : value;
      }
      return value;
    },
    getParentLogicObjectName(logicItem) {
      if (logicItem && logicItem.request && logicItem.request.parentUid) {
        return `Object with UID: ${logicItem.request.parentUid} (Details needed from parent)`;
      }
      return 'Unknown Parent Object';
    }
  }
};
</script>

